.har {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    padding: 0px;
    min-height: 40vh;
    width: 100vw;
    background-color: rgb(0, 0, 0);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
  
    @include tablet{
      margin: 0;
    }
  }
  
  .har__container {
    width: 100%;
    align-self: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    margin-left: auto;
    margin-right: auto;
    transition: transform 0.5s ease-in-out; /* Анимация будет применяться к свойству transform */
  
  }
  
  
  
  /* Остальные стили остаются без изменений */
  .har__content {
  display: flex;
  flex-direction: column;
  position: relative;
  flex-shrink: 0;
  box-sizing: border-box;
  margin-top: 0px;
  background-image: url(../../img/svg/title.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  min-height: 40vh;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  }
  
  
  .har__title {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 200px;
    line-height: 0.9em;
    height: auto;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    font-family: America-Compressed-Black, sans-serif;
    color: rgb(205, 205, 205);
    font-size: 80px;
    margin-bottom: 20px;
    line-height: 100%;
  
  
    @include tablet{
      font-size: 70px ;
  
    }
  
    @include mobile {
      font-size: 50px;
      
    }
  }
  
  
  
  
  
  