.popup {
    pointer-events: none;
    visibility: hidden;
    position: fixed;
    inset: 0;
    display: flex;
    justify-content: center;
    opacity: 0;
    z-index: 999;
    backdrop-filter: blur(3px); /* Применяем размытие заднего фона только к попапу */
    background-color: rgba(0, 0, 0, 0.3); /* Чтобы сделать попап немного затемненным */
    background-color: hsl(var(--nf, var(--n)) / var(--tw-bg-opacity));
    --tw-bg-opacity: 0.4;
    transition-duration: 0.2s;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-property: transform, opacity, visibility;
    overflow-y: hidden;
    overscroll-behavior: contain;
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    overflow-y: scroll;
    align-items: center;
    grid-auto-flow: column;
    color: white;
    display: grid;

    @include middle-tablet {
        padding-top: 2.5rem;
        padding-bottom: 2.5rem;
    }
}

.popup--open {
    pointer-events: auto;
    visibility: visible;
    opacity: 1;
}

.popup__section {
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    max-width: 64rem;
    width: auto;
    display: block;

    @include middle-tablet {
        width: auto;
        width: 28rem;
    }

    @include mobile {
        width: auto;
    }
}

/* Your existing styles for .popup and .popup__open */

.popup__container {
    display: block;

    @include middle-tablet {
       
    }
}

.popup__one {
    justify-content: flex-end;
    flex-direction: column;
    display: flex;
    position: relative;
}

.popup__image {
    --tw-brightness: brightness(0.75);
    filter: var(--tw-blur) var(--tw-brightness) var(--tw-contrast) var(--tw-grayscale) var(--tw-hue-rotate)
        var(--tw-invert) var(--tw-saturate) var(--tw-sepia) var(--tw-drop-shadow);
    background-image: linear-gradient(to bottom, #5b2f2f, #674c4c);
    max-width: 100%;
    display: block;
    vertical-align: middle;
}

.popup__close {
    position: absolute;
    right: -2.5rem;
    padding: 0.5rem;
    top: -2rem;
    cursor: pointer;
    border: none;
}

.popup__svg--two {
    display: block;
    vertical-align: middle;
}

.popup__row {
    text-transform: uppercase;
    font-size: 0.75rem;
    line-height: 1rem;
    vertical-align: middle;
    padding-bottom: 4rem;
    padding-left: 3.5rem;
    padding-right: 3.5rem;
    align-items: center;
    flex-direction: row;
    max-width: 64rem;
    width: 100%;
    display: flex;
    position: absolute;
    justify-content: space-between;
    background-image: linear-gradient(to bottom, rgb(129 129 133), rgb(57 68 94));

    @include middle-tablet {
        flex-direction: column;
        justify-content: center;
        max-width: 100%;
    }
}

.popup__row--button {
    font-family: SohneMono, sans-serif;
    gap: 1rem;
    position: absolute;
    top: 0.5rem;
    flex-direction: row;
    display: flex;

    @include middle-tablet {
        flex-direction: column;
        width: 100%;
        position: absolute;
        display: flex;
    }
}

.popup__button {
    max-width: 200px;
    top: 0;
    left: -2rem;
    width: 95%;
    position: relative;
    letter-spacing: 0.1em;
    font-size: 0.75rem;
    background-color: rgb(255, 255, 255);
    border-radius: 9999px;
    gap: 0.5rem;
    border-color: currentColor;
    background-color: transparent;
    flex-shrink: 0;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    flex-wrap: wrap;
    border-color: transparent;
    border-color: hsl(var(--n) / var(--tw-border-opacity));
    text-align: center;
    height: 3rem;
    padding-left: 1rem;
    padding-right: 1rem;
    line-height: 1em;
    min-height: 3rem;
    font-weight: 200;
    text-transform: uppercase;
    text-transform: var(--btn-text-case, uppercase);
    text-decoration-line: none;
    border-width: 1px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    -webkit-appearance: button;
    transition:
        background-color 0.5s ease,
        color 0.5s ease,
        fill 0.5s ease; /* добавлено */

    @include middle-tablet {
        width: 100%;
        margin-right: auto;
        margin-left: auto;
        padding-left: 0;
        padding-right: 0;
        left: 0;
        max-width: 90%;
    }
}

.popup__button:hover {
    background-color: white; /* добавлено */
    color: black; /* добавлено */
}



.popup__row--music {
    gap: 0.5rem;
    justify-content: center;
    align-items: center;
    display: flex;
    margin-top: 1.5rem;

    @include middle-tablet {
        display: none;
    }
}

.poput__music--paragraph {
    letter-spacing: 0.1em;
    font-family: SohneMono, sans-serif;
    text-align: right;
    margin-right: 0.75rem;

    @include middle-tablet {
        display: none;
    }
}

.popup__artist--image {
    border-radius: 9999px;
    max-width: 100%;

    @include middle-tablet {
        display: none;
    }
}

.popup__mobile--music {
    color: rgb(255 255 255);
    text-transform: uppercase;
    font-size: 0.875rem;
    line-height: 1.25rem;
    font-family: SohneMono;
    margin-left: 2rem;
    margin-bottom: 1.25rem;
    z-index: 10;
    visibility: hidden;

    @include middle-tablet {
        display: none;
    }
}

/* Styles for the entire section */
.popup__row--two {
    vertical-align: middle;
    padding-top: 2rem;
    padding-bottom: 2rem;
