        padding-bottom: 0.5rem;
        padding-left: 0.5rem;
        padding-right: 0.5rem;
        flex-direction: column;
        align-items: center;
    }
}

/* Styles for the left column */
.popup__column--one {
    display: flex;
    max-width: 28rem;
    flex-direction: column;
    justify-content: flex-start;
    gap: 1rem;

    @include middle-tablet {
    }
}

.popup__column--one--paragraph {
