    line-height: 2.5rem;
    font-family: SohneBreit, sans-serif;
    text-align: left;

    @include middle-tablet {
        font-size: 1.5rem;
        line-height: 2rem;
        padding-left: 1.25rem;
        padding-right: 1.25rem;
        padding-bottom: 50px;
        width: 90%;
    }
}

.popup__column--one--text {
    font-size: 1.125rem;
    line-height: 1.75rem;
