    margin-top: 1.25rem;

    @include middle-tablet {
        display: none;
    }
}

.popup__border {
    margin-left: 2.25rem;
    margin-right: 2.25rem;
    min-height: 340px;
    width: 1px;
    background-color: rgb(255 255 255);
    opacity: 1;

    @include middle-tablet {
        display: none;
    }
}

.popup__grid {
    gap: 1.25rem;
    align-items: center;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    max-width: 350px;
    display: grid;

    @include middle-tablet {
        grid-template-columns: repeat(2, minmax(0, 1fr));
        align-items: center;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 50px;
    }
}

