.popup__svg--symbols {
    bottom: 0;
    top: 4.35rem;
    position: absolute;
    display: block;
    vertical-align: middle;
}

.popup__svg--symbols.open {
    rotate: 180deg; /* Поворачиваем стрелку вверх */
    top: 1.45rem; /* Изменяем значение top */
}
