.vaccination {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    padding: 20px;
    min-height: 100px;
    width: 100vw;
    margin-left: calc(50% - 50vw);
    color: rgb(0, 0, 0);
    background-color: rgb(239, 199, 180);
}

.vaccination__container {
    width: 100%;
    align-self: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    box-sizing: border-box;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    margin-left: auto;
    margin-right: auto;
}

.vaccination__column--one {
    display: flex;
    flex-direction: row;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 20px;

    @include middle-tablet {
        flex-direction: column;
    }
}

.vaccination__content--one {
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    line-height: normal;
    width: calc(33.3333% - 13.3333px);
    margin-left: 0px;

    @include middle-tablet {
       width: 100%;
       margin-left: 0;
       margin-right: 0;
    }
}

