.vaccination__item--one {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    height: 100%;
    padding: 30px;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(0, 0, 0);
    background-color: rgb(243, 243, 243);
    -webkit-box-pack: center;
    justify-content: center;
}

.vaccination__item--two {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    height: 100%;
    padding: 30px;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(254, 254, 254);
    background-color: rgb(0, 0, 0);
    -webkit-box-pack: center;
    justify-content: center;
}

.vaccination__title {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    line-height: 20px;
    height: auto;
    text-align: center;
    font-family: HelveticaNeue-Bold, sans-serif;
    font-size: 18px;
}

.vaccination__description {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 10px;
    line-height: 18px;
    height: auto;
    text-align: center;
    font-family: HelveticaNeue-Light, sans-serif;
    font-size: 14px;
}

.vaccination__button {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 25px;
    appearance: none;
    padding: 15px 35px;
    background-color: rgba(0, 0, 0, 0);
    color: rgb(0, 0, 0);
    border-radius: 100px;
    text-align: center;
    cursor: pointer;
    border-width: 1px;
    border-style: solid;
    backdrop-filter: blur(10px);
    text-transform: uppercase;
    font-family: SohneMono, sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
    width: auto;
    align-self: center;
    margin-right: 0px;
    border-color: rgb(0, 0, 0);
    transition: all 0.5s cubic-bezier(0.37, 0.01, 0, 0.98) 0s;
}

.vaccination__button:hover {
    background-color: rgb(0, 0, 0);
    color: rgb(255, 255, 255);
}

