.partners-grid {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgb(225 162 132);
    padding: 0 20% 5rem;

    @include mobile {
        padding: 0 5% 5rem; // Adjust padding for smaller screens
    }

    .partners-grid_list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-gap: 1.2375rem;
        list-style: none;
        padding: 0;

        @include small-tablet {
            grid-template-columns: repeat(3, 1fr); // maintain 3 columns
            grid-gap: 20px;
        }

        @include small-mobile {
            grid-template-columns: 1fr; // Switch to single column layout for smaller screens
        }
    }

    .partners-grid__card {
        box-sizing: border-box;
        text-align: center;
        border: 1px solid rgb(255, 255, 255);
        background-color: #ebf7f6;

        a {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 100%;
            padding: 1rem 0.5rem;
            color: inherit;

            @include small-tablet {
                padding: 0.7rem 0.3rem;
            }
        }
    }

    .partners-grid__card-logo {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-grow: 1;

        img {
            max-width: 100%;
            height: auto;
        }
    }

    .partners-grid__card-content {
        padding: 1.5rem;

        
    }
    .partners-grid__card-title {
        margin: 0;
        padding: 1rem 0;
        color: rgb(11, 11, 11);
        font-weight: bold;

        @include mobile {
            font-size: 1rem; // Adjust font size for smaller screens
        }
    }
}
