.politics {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    padding: 20px;
    min-height: 100px;
    width: 100vw;
    margin-left: calc(50% - 50vw);
    background-color: rgb(239, 199, 180);
}

.politics__container {
    width: 100%;
    align-self: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    box-sizing: border-box;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    margin-left: auto;
    margin-right: auto;
}

.politics__heading {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 30px;
    line-height: 65px;
    height: auto;
    text-align: center;
    font-family: SohneBreit-ExtrafettKursiv, sans-serif;
    color: rgb(255, 255, 255);
    font-size: 70px;
    padding-bottom: 0px;

    @include mobile {
        font-size: 50px;
    }
}

.politics__title {
    /* стили для заголовка .politics__title */
}

.politics__bta {
    display: flex;
    flex-direction: row;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;

    @include mobile {
        flex-direction: column;
    }
}

.politics__button--one {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 25px;
    appearance: none;
    padding: 15px 35px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border-radius: 100px;
    text-align: center;
    cursor: pointer;
    border-width: 1px;
    border-style: solid;
    backdrop-filter: blur(10px);
    text-transform: uppercase;
    font-family: SohneMono, sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
    width: auto;
    align-self: center;
    margin-right: 25px;
    margin-bottom: 40px;
    transition: all 0.5s cubic-bezier(0.37, 0.01, 0, 0.98) 0s;

    @include mobile {
        margin-left: auto;
        margin-right: auto;
    }
}

.politics__button--one:hover {
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
}

.politics__button--two {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 25px;
    appearance: none;
    padding: 15px 35px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border-radius: 100px;
    text-align: center;
    cursor: pointer;
    border-width: 1px;
    border-style: solid;
    backdrop-filter: blur(10px);
    text-transform: uppercase;
    font-family: SohneMono, sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
    width: auto;
    align-self: center;
    margin-right: 0px;
    margin-bottom: 40px;
    transition: all 0.5s cubic-bezier(0.37, 0.01, 0, 0.98) 0s;

    @include mobile {
        margin-left: auto;
        margin-right: auto;
        margin-top: 0px;

    }
}

.politics__button--two:hover {
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
}
