.diversity {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    padding: 20px 20px 80px;
    min-height: 100px;
    background-color: rgb(9 19 33);
    width: 100vw;
    margin-left: calc(50% - 50vw);
}

.diversity__container {
    width: 100%;
    align-self: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    box-sizing: border-box;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    margin-left: auto;
    margin-right: auto;
}

.diversity__title--one {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    line-height: 65px;
    height: auto;
    color: rgb(255, 255, 255);
    font-family: SohneBreit-ExtrafettKursiv, sans-serif;
    text-align: left;
    font-size: 80px;
    font-weight: bold;
    margin: 80px auto 0px -60px;
    max-width: 700px;

    @include middle-tablet {
        text-align: center;
        margin-right: auto;
        margin-left: auto;
    }

    @include mobile {
        font-size: 50px;
    }

    @include mini-mobile {
        font-size: 45px;
        line-height: 40px;
    }
}

.diversity__title--two {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    line-height: 50px;
    height: auto;
    color: rgb(255, 255, 255);
    font-family: SohneBreit-fettKursiv, sans-serif;
    text-align: right;
    font-size: 55px;
    margin: 10px -30px 40px auto;
    max-width: 1200px;

    @include middle-tablet {
        text-align: center;
        margin-right: auto;
        margin-left: auto;
    }

    @include mobile {
        font-size: 45px;
    }

    @include mini-mobile {
        font-size: 40px;
        line-height: 40px;
    }
}

.diversity__title-text {
}

.diversity__description {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 20px;
    line-height: 20px;
    height: auto;
    text-align: center;
    color: rgb(255, 255, 255);
    font-size: 16px;
    padding-left: 0px;
    padding-right: 0px;
    font-family: HelveticaNeue-Light, sans-serif;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 30px;
    border-width: 1px 0px 0px;
    border-top-style: solid;
    border-bottom-style: initial;
    border-bottom-color: initial;
    border-left-style: initial;
    border-left-color: initial;
    border-right-style: initial;
    border-right-color: initial;
}

.diversity__description-text {
    /* Ваши стили для текста описания */
}

.diversity__button {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 40px;
    appearance: none;
    padding: 15px 35px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border-radius: 100px;
    text-align: center;
    cursor: pointer;
    border-width: 1px;
    border-style: solid;
    backdrop-filter: blur(10px);
    text-transform: uppercase;
    font-family: SohneMono, sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
    width: auto;
    align-self: center;
    line-height: normal;
    margin-bottom: 40px;
    transition: background-color 0.5s ease, color 0.5s ease; /* добавлено */
}

.diversity__button:hover {
    background-color: white; /* добавлено */
    color: black; /* добавлено */
}


