@mixin for-desktop {
  @media (max-width: 1193px) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: 1440px) {
    @content;
  }
}



@mixin tablet {
  @media (max-width: 1024px) {
    @content;
  }
}

@mixin middle-tablet{
  @media (max-width: 999px){
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: 768px) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: 576px) {
    @content;
  }
}

@mixin middle-mobile {
  @media (max-width: 500px) {
    @content;
  }
}

@mixin mini-mobile {
  @media (max-width: 400px) {
    @content;
  }
}

@mixin small-mobile {
  @media (max-width: 375px) {
    @content;
  }
}
