/* Стили для секции .navigation */
.navigation {
    padding: 80px 0 20px 0;
    position: relative;
    color: #ffffff;
    display: block;
    background-color: #4b6379;

}

/* Стили для контейнера .navigation__container */
.navigation__container {
    width: 1140px;
    justify-content: space-between;
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    max-width: 92%;
}

/* Стили для .navigation__content */
.navigation__content {
    order: 1;
    position: relative;
    margin-left: 0.75%;
    margin-right: 0.75%;
    width: 40.1666666667%;
    display: block;

    @include small-tablet {
        order: 0;
        width: 98.5%;
    }
}

/* Стили для заголовка .navigation__title */
.navigation__title {
    margin-bottom: 6px;
    letter-spacing: -0.5px;
    color: #fefefe;
    font-size: 40px;
    line-height: 1;
    font-family: Souvenir, serif;

    @include small-tablet {
        text-align: center;
        margin-bottom: 12px;
    }
}

/* Стили для описания на десктопе .navigation__desktop-description */
.navigation__desktop-description {
    display: block;

    @include small-tablet {
        display: none;
    }
}

.navigation__desktop-description p {
    display: block;
    line-height: 2;
}

/* Стили для описания на мобильных устройствах .navigation__mobile-description */
.navigation__mobile-description {
    display: none;
    font-size: 14px;
    text-align: center;

    @include small-tablet {
        text-align: center;
        display: block;
        font-size: 14px;
        line-height: 1rem;
        padding-top: 2rem;
    }
}

.navigation__link {
    display: block;
    margin-top: 10px;
    text-decoration: underline;
    color: #248742;
    cursor: pointer;
}

/* Стили для кнопки .navigation__button */
.navigation__button {
    padding: 8px 24px 6px 34px;
    background-color: #248742;
    border-radius: 46px;
    margin: 32px 0 20px 0;
    display: inline-block;
    transition: all 0.3s ease-in;
    color: white;
}

.navigation__button:hover .arrow {
    margin-left: 10px; /* Move .arrow to the right when hovering over .devices__button */
    transition: all 0.3s ease-in-out; /* Add smooth transition for the animation */
}

/* Стили для изображения .navigation__image */
.navigation__image {
    order: 2;
    position: relative;
    margin-left: 0.75%;
    margin-right: 0.75%;
    width: 48.5%;

    @include small-tablet {
        order: 0;
        width: 98.5%;
    }
}

/* Стили для Lazy-загрузки изображения .navigation__lazy */
.navigation__lazy {
    width: 100%;
}

/* Стили для стрелки .navigation__arrow */
.navigation__arrow {
    display: inline-block;
    vertical-align: middle;
    margin-left: 5px;
    margin-right: 10px;
    margin-bottom: 2px;
    transition: all 0.2s ease-in-out;
}

.navigation__arrow img {
    display: inline-block;
    vertical-align: middle;
    width: 22px;
    filter: brightness(500%);
}
