.tems {
  display: flex;
  flex-direction: column;
  position: relative;
  flex-shrink: 0;
  box-sizing: border-box;
  margin-top: 0px;
  padding: 0px;
  background-color: rgb(225 162 132);
  width: 100vw;
  margin-left: calc(50% - 50vw);
  margin-bottom: 0px;
  height: 80vh;
}

.tems__container {
  width: 100%;
  align-self: stretch;
  -webkit-box-flex: 1;
  flex-grow: 1;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  -webkit-box-align: stretch;
  align-items: stretch;
  margin-left: auto;
  margin-right: auto;
}

.tems__main {
  display: flex;
  flex-direction: row;
  position: relative;
  flex-shrink: 0;
  box-sizing: border-box;
  margin-top: 0px;
  padding-top: 50px;

  @include middle-tablet {
    flex-direction: column;
  }
}

.tems__content {
  display: flex;
  flex-direction: column;
  -webkit-box-align: stretch;
  align-items: stretch;
  line-height: normal;
  width: calc(50% - 0px);
  margin-left: 0px;

  @include middle-tablet {
    width: 100%;
    margin-left: 0px;
  }
}

.tems__title {
  display: flex;
  flex-direction: column;
  position: relative;
  flex-shrink: 0;
  box-sizing: border-box;
  line-height: 80px;
  height: auto;
  font-family: SohneBreit-ExtrafettKursiv, sans-serif;
  color: rgb(255, 255, 255);
  margin-left: auto;
  margin-right: auto;
  font-size: 80px;
  font-weight: bold;
  width: 70%;

  @include middle-tablet {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    text-align: center;
    margin-bottom: 50px;
  }

  @include mobile {
    font-size: 45px;
    line-height: 50px;

  }
}

.tems__slider {
  display: flex;
  flex-direction: column;
  -webkit-box-align: stretch;
  align-items: stretch;
  line-height: normal;
  width: calc(50% - 0px);
  margin-left: 20px;

  @include middle-tablet {
    width: 40%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
  }

  @include mobile {
    width: 50%;
  }

  @include middle-mobile {
    width: 60%;
  }

  @include mini-mobile {
    width: 80%;
  }

  @include small-mobile {
    width: 90%;
  }
}

.tems__slide {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #ecece8;
  color: #001f1e;
  margin-right: 22px;
  margin-bottom: 27px;
  padding: 33px 42px ;
  width: 400px; // replace with your desired width
  height: 500px; // replace with your desired height
  border: 2px solid #374e64;

@include middle-tablet {
  margin-left: auto;
  margin-right: auto;
  height: 500px;
  width: 100%;
}

@include small-tablet {
  height: 400px;
}
}

.tems__picture {
  position: relative;
  width: 100%;
  height: auto;
  max-width: 100%;
  max-height: 80%;
}

.tems__picture img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.tems__decoration {
  width: 100%;
  padding-top: 100%;
  pointer-events: none;
  font-size: 0px;
}

.tems__description {
  display: flex;
  flex-direction: column;
  position: relative;
  flex-shrink: 0;
  box-sizing: border-box;
  margin-top: 20px;
  text-align: center;
  height: auto;
  padding-left: 15px;
  padding-right: 15px;
  color: #000;
}

.tems__controls {
  align-items: center;
  grid-gap: 0.69444rem;
  gap: 0.69444rem;
}

.round-button {
  cursor: pointer;
  overflow: hidden;
  position: relative;
  height: 5.15459rem;
  width: 5.15459rem;
  transition: transform 0.5s cubic-bezier(0.25, 1, 0.5, 1);
  will-change: opacity;
}

.round-button:hover {
  transform: scale(0.7);
}

.round-button.isDisable {
  opacity: 0.3;
  pointer-events: none;
}

.round-button__arrow {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 1.13092rem;
  height: 1.13092rem;
  margin: auto;
  background-position: 50%;
  pointer-events: none;
  background-size: cover;
  background-color: transparent;
  border: none;
}

.round-button .round-button__circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transition: transform 0.5s cubic-bezier(0.25, 1, 0.5, 1);
}

.round-button--white .round-button__circle {
  background-color: #fff;
}

.round-button--green .round-button__circle {
  background-color: #001f1e;
}

.round-button--black .round-button__circle {
  background-color: #000;
}

.mobile {
  display: none;

  @include middle-tablet {
    display: flex;
    margin-right: auto;
    margin-left: auto;
  }
}

.desktop {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  padding-top: 15rem;
  width: 50%;

  @include middle-tablet {
    display: none;
  }
}
