.harmon {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    padding: 0px 0px 80px;
    min-height: 100px;
    background-color: rgb(225 162 132);
    margin-left: calc(50% - 50vw);
    width: 100vw;
}

.harmon__container {
    width: 100%;
    align-self: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    margin-left: auto;
    margin-right: auto;
    transition: transform 0.5s ease-in-out; /* Анимация будет применяться к свойству transform */
}

/* Остальные стили остаются без изменений */
.harmon__content {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;

    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

.harmon__title {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 200px;
    line-height: 0.9em;
    height: auto;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    font-family: America-Compressed-Black, sans-serif;
    color: rgb(0, 0, 0);
    font-size: 80px;
    margin-bottom: 20px;
    line-height: 100%;
    font-weight: bold;

    @include tablet {
        font-size: 70px;
    }

    @include mobile {
        font-size: 50px;
    }
}

.harm {
    width: 50%;
    margin-left: auto;
    margin-right: auto;

    @include middle-tablet {
        width: 100%;
    }
}
