.map {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    padding: 0px;
    min-height: 100vh;
    width: 100vw;
    margin-left: calc(50% - 50vw);
    background-color: rgb(0, 0, 0);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    border-width: 0px;
    border-top-style: solid;
    border-bottom-style: solid;
    border-left-style: initial;
    border-left-color: initial;
    border-right-style: initial;
    border-right-color: initial;

    @include tablet {
        margin: 0;
    }
}

.map__container {
    width: 100%;
    align-self: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    margin-left: auto;
    margin-right: auto;
}

.map__content {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    background-image: url(../../img/svg/road.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    border-top-style: solid;
    border-bottom-style: solid;
    border-left: 0px;
    border-right: 0px;
    min-height: 100vh;
    width: 100%;
    padding-left: 70px;
    padding-right: 80px;

    @include middle-tablet {
        background-image: url(../../img/svg/road.png);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        }
        
        @include mobile{
        padding-left: 10px;
        background-position: left left;
        }
}

.map__content::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Adjust the alpha value to control darkness */
    z-index: 1; /* Place the overlay behind the content */
}

.map__title {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 150px;
    line-height: 100%;
    font-weight: bold;
    height: auto;
    margin-right: auto;
    text-align: left;
    font-family: America-Compressed-Black, sans-serif;
    color: rgb(255, 255, 255);
    font-size: 80px;
    margin-bottom: 20px;
    z-index: 2;

    @include tablet {
        font-size: 60px;
        line-height: 100%;
    }

    @include mobile {
        font-size: 35px;
    }
}

.map__description {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 10px;
    line-height: 18px;
    height: auto;
    margin-right: auto;
    text-align: left;
    font-family: SohneMono, sans-serif;
    color: rgb(255, 255, 255);
    font-size: 12px;
    margin-bottom: 40px;
    letter-spacing: 1px;
    font-weight: 700;
    z-index: 2;
}

.map__button {
    display: flex;
    flex-direction: row;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    width: auto;
    align-self: center;
    margin-bottom: 120px;
    margin-right: auto;
    z-index: 2;

}

.map__btn--one {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 25px;
    appearance: none;
    padding: 15px 35px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: 1px solid #248742;
    border-radius: 100px;
    text-align: center;
    cursor: pointer;
    border-width: 1px;
    border-style: solid;
    backdrop-filter: blur(10px);
    text-transform: uppercase;
    font-family: SohneMono, sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
    width: auto;
    align-self: center;
    margin-right: 25px;

@include mobile {
    font-size: 8px;
}
}

.map__btn--two {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 25px;
    appearance: none;
    padding: 15px 35px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: 1px solid #248742;
    border-radius: 100px;
    text-align: center;
    cursor: pointer;
    border-width: 1px;
    border-style: solid;
    backdrop-filter: blur(10px);
    text-transform: uppercase;
    font-family: SohneMono, sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
    width: auto;
    align-self: center;
    margin-right: 25px;

    @include mobile {
        font-size: 8px;
    }

}

.map__btn--one,
.map__btn--two {
    transition: all 0.5s cubic-bezier(0.37, 0.01, 0, 0.98) 0s;
}

.map__btn--one:hover,
.map__btn--two:hover {
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
}
