/* Стили для класса modboard */
.modboard {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    padding: 0px 70px 80px;
    min-height: 100px;
    background-color: rgb(225 162 132);
    margin-left: calc(50% - 50vw);
    width: 100vw;
}

/* Стили для класса modboard__container */
.modboard__container {
    width: 100%;
    align-self: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    margin-left: auto;
    margin-right: auto;
}

/* Стили для класса modboard__columns */
.modboard__columns {
    display: flex;
    flex-direction: row;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 70px;
    margin-bottom: 70px;

    @include small-tablet {
        flex-direction: column;
    }
}

/* Стили для класса container__paragraph и container__paragraph-item */
.container__paragraph {
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    line-height: normal;
    width: calc(50% - 10px);
    margin-left: 0px;

    @include small-tablet {
        width: 100%;
        justify-content: center;
        text-align: center;
    }
}

/* Стили для класса item__title */
.container__paragraph-item {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    line-height: 0.8em;
    height: auto;
    font-size: 110px;
    font-family: America-Compressed-Black, sans-serif;
    font-weight: 100;
    width: auto;
    align-self: center;
    color: rgb(0, 0, 0);
    text-align: left;
    margin-bottom: 0px;
    margin-right: auto;
    font-weight: bold;

    @include tablet {
        font-size: 50px;
    }

    @include small-tablet {
        width: 100%;
        justify-content: center;
        text-align: center;
        line-height: 1em;
      
    }

    @include mobile {
        font-size: 26px;
    }
}

.item__subtitle {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 40px;
    line-height: 0.8em;
    height: auto;
    font-size: 40px;
    font-family: SohneMono, sans-serif;
    font-weight: 100;
    width: auto;
    align-self: center;
    color: #c5fe49;
    text-shadow: rgb(0, 0, 0) 0px 0px 20px;
    margin-left: 0px;
    letter-spacing: 3px;
    margin-right: auto;
    cursor: pointer;
    pointer-events: auto;

    @include tablet {
        font-size: 30px;
    }

    @include small-tablet {
        width: 100%;
        justify-content: center;
        text-align: center;
        line-height: 1em;
        margin-bottom: 50px;
        margin-left: auto;
        margin-right: auto;
        font-size: 25px;
    }
}

/* Стили для класса container__photo и photo__columns */
.container__photo {
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    line-height: normal;
    width: calc(50% - 10px);
    margin-left: 20px;

    @include small-tablet {
        width: 100%;
        justify-content: center;
        text-align: center;
        line-height: 1em;
        margin-left: 0;
    }
}

/* Стили для класса container__modboard-photo и container__column-photo */
.photo__columns {
    display: flex;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    padding-bottom: 0px;
    -webkit-box-pack: justify;
    justify-content: space-between;
    width: auto;
    align-self: stretch;
    flex-flow: row wrap;
}

.modboard__image {
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    padding-bottom: 0px;
    width: 33%;
    margin-bottom: 7px;

    @include small-tablet {
        width: 49%;
        justify-content: center;
        text-align: center;
        line-height: 1em;
        margin-bottom: 10px;
    }
}

.photo__img {
    opacity: 1;
    transition: opacity 0.2s ease-in-out 0s;
    object-fit: cover;
    object-position: center center;
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0px;
    top: 0px;
}

.modboard__image--sizer {
    width: 100%;
    padding-top: 79.05%;
    pointer-events: none;
    font-size: 0px;
}

.modboard__link {
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
}

.modboard__instagram {
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    height: auto;
    width: auto;
    align-self: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    -webkit-box-pack: center;
    justify-content: center;
    opacity: 0;
}
