.title-text-sample {
    background-color: rgb(225 162 132);
    display: flex;
    justify-content: center;
    overflow: auto; // Enable scrollbars when content exceeds the container's boundaries
    width: 100%; // Width set to 100%
    min-height: 10vh; // Minimum height set to 100% of the viewport height

    .container {
        max-width: 1000px;
        width: 100%;
        color: rgb(255, 255, 255);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 50px; // Padding added to prevent content from touching the edges
        margin-top: 0rem;

        .title-text-sample__title {
            text-align: center;
            width: 90%;
            font-weight: bold;
            font-size: 60px;

            @include mobile {
                font-size: 35px;
                margin-left: auto;
                margin-right: auto;
            }
        }
        @include mobile {
            font-size: 40px;
            margin-left: auto;
            margin-right: auto;
            margin-top: 15px;
        }  
    }

    &.small-size {
        min-height: 10vh; // Reduce the minimum height to 50% of the viewport height
        // Add any other changes you want to make here
    }
}
