.cont {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    padding: 20px;
    min-height: 100px;
    width: 100vw;
    margin-left: calc(50% - 50vw);
    background-color: rgb(239, 199, 180);
   
}

.cont__container {
    width: 100%;
    align-self: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    box-sizing: border-box;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    margin-left: auto;
    margin-right: auto;
}

.cont__title {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 20px;
    line-height: normal;
    height: auto;
    font-family: SohneBreit-ExtrafettKursiv, sans-serif;
    color: rgb(0, 0, 0);
    font-size: 75px;
    text-align: center;
    padding-bottom: 30px;

    @include mobile {
        font-size: 50px;
    }
}