.pincod__section {
    max-width: 1800px;
    height: 100%;
    margin-right: auto;
    margin-left: auto;
    background-color: rgba(55, 50, 47, 0.825);
}
.pincod__container {
    padding-left: 100px;
    padding-right: 100px;
    padding-top: 100px;
    padding-bottom: 100px;

    @include small-tablet {
        padding-left: 20px;
        padding-right: 20px;
        padding-top: 40px;
        padding-bottom: 40px;
    }
}
.pincod__title {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    opacity: 1;
    color: rgb(255 255 255);
    letter-spacing: -0.01em;
    line-height: 95%;
    font-weight: 700;
    font-size: 75px;
    font-family: GTAmericaStandardBold;

    @include middle-tablet {
        font-size: 55px;
    }

    @include mobile {
        font-size: 35px;
    }
}
.pincod__grid {
    text-align: left;
    column-gap: 5rem;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    grid-auto-flow: row dense;
    display: grid;
    margin-top: 80px;

    @include small-tablet {
        margin-top: 50px;
    }
}
.pincod__mobile--version {
    display: none;
    margin-bottom: 50px;
    grid-column: span 3 / span 3;

    @include tablet {
        display: grid;
        margin-bottom: 50px;
        grid-column: span 3 / span 3;
    }
}
.pincod__content--mobile {
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
}
.pincod__item--mobile {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    height: auto;
    background-color: rgb(255, 98, 0);
    padding-bottom: 30px;
}
.pincod__title--mobile {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 20px;
    line-height: normal;
    height: auto;
    text-align: center;
    font-family: America-Compressed-Black, sans-serif;
    color: rgb(255, 255, 255);
    font-size: 50px;
}
.pincod__text--mobile {
    outline: none;
}

.pincod__content--dekstop {
    grid-column: span 2 / span 2;

    @include tablet {
        grid-column: span 3 / span 3;
    }
}

.pincod__content--one {
    row-gap: 2rem;
    -moz-column-gap: 2rem;
    column-gap: 2rem;
    grid-template-columns: repeat(6, minmax(0, 1fr));
    grid-auto-flow: row dense;
    display: grid;
}

.pincod__column--top {
    grid-column: span 6 / span 6;
}

.pincod__form {
    row-gap: 2rem;
    -moz-column-gap: 2rem;
    column-gap: 2rem;
    grid-template-columns: repeat(6, minmax(0, 1fr));
    grid-auto-flow: row dense;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    display: grid;
}
.pincod__stroke--one {
    text-align: left;
    grid-column: span 6 / span 6;
}
.pincod__label {
    opacity: 0.4;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    opacity: 1;
    color: rgb(255 255 255);
    letter-spacing: 0.12em;
    line-height: 20px;
    text-transform: uppercase;
    font-size: 12px;
    font-family: GTAmericaMonoMedium;
}
.pincod__input {
    opacity: 1;
    color: rgb(255 255 255);
    text-transform: uppercase;
    font-weight: 700;
    font-size: 2.25rem;
    line-height: 2.5rem;
    font-family: GTAmericaStandardBold;
    padding-bottom: 10px;
    background-color: transparent;
    border-bottom: 1px solid white;
    width: 100%;
    margin-top: 10px;

    @include small-tablet {
        font-size: 1.25rem;
    }
}

.pincod__input--two {
    opacity: 1;
    color: rgb(255 255 255);
    text-transform: uppercase;
    font-weight: 700;
    font-size: 2.25rem;
    line-height: 2.5rem;
    font-family: GTAmericaStandardBold;
    padding-bottom: 10px;
    background-color: transparent;
    border-bottom: 1px solid white;
    width: 100%;
    margin-top: 10px;

    @include small-tablet {
        font-size: 0.7rem;
        padding-bottom: 5px;
        line-height: 1.5rem;
    }
}

.pincod__border {
    color: red;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    letter-spacing: 0.12em;
    line-height: 20px;
    text-transform: uppercase;
    font-size: 12px;
    font-family: GTAmericaMonoMedium;
    padding-top: 5px;
    height: 20px;
}

.pincod__stroke--left {
    text-align: left;
    grid-column: span 2 / span 2;

    @include small-tablet {
        grid-column: span 6 / span 6;
    }
}

.pincod__stroke--right {
    text-align: left;
    grid-column: span 2 / span 2;

    @include small-tablet {
        grid-column: span 3 / span 3;
    }
}

.pincod__item--container {
    opacity: 1;
    color: rgb(255 255 255);
    text-transform: uppercase;
    font-weight: 700;
    font-size: 2.25rem;
    line-height: 2.5rem;
    font-family: GTAmericaStandardBold;
    padding-bottom: 10px;
    background-color: transparent;
    border-bottom-width: white solid 2px;
    width: 100%;
    margin-top: 10px;
}

.pincod__container--text {
    position: relative;
    box-sizing: border-box;
}

.pincod__empty {
    z-index: 9999;
    border: 0px;
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    width: 1px;
    position: absolute;
    overflow: hidden;
    padding: 0px;
    white-space: nowrap;
}

.pincod__control {
    -webkit-box-align: center;
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-wrap: wrap;
    -webkit-box-pack: justify;
    justify-content: space-between;
    min-height: 38px;
    position: relative;
    transition: all 100ms ease 0s;
    border-radius: 4px;
    box-sizing: border-box;
    border: none;
    background: #333;
    text-align: start;
    outline: 0px !important;
}

.pincod__control--one {
    -webkit-box-align: center;
    align-items: center;
    display: grid;
    flex: 1 1 0%;
    flex-wrap: wrap;
    position: relative;
    overflow: visible;
    padding: 0px;
    box-sizing: border-box;
    border-bottom: 1px solid white;
}

.pincod__control--two {
    grid-area: 1 / 1 / 2 / 3;
    color: white;
    margin-left: 2px;
    margin-right: 2px;
    box-sizing: border-box;
    top: 0%;
    transition:
        top 0.1s ease 0s,
        font-size 0.1s ease 0s;
}

.pincod__control--text {
    opacity: 0.15;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 2.25rem;
    line-height: 2.5rem;

    @include small-tablet {
        font-size: 1.25rem;
    }
}

.pincod__input--one {
    background: 0px center;
    border: 0px;
    caret-color: transparent;
    font-size: inherit;
    grid-area: 1 / 1 / 2 / 3;
    outline: 0px;
    padding: 0px;
    width: 1px;
    color: transparent;
    left: -100px;
    opacity: 0;
    position: relative;
    transform: scale(0.01);
}

.pincod__indicator {
    -webkit-box-align: center;
    align-items: center;
    align-self: stretch;
    display: flex;
    flex-shrink: 0;
    box-sizing: border-box;
}

.pincod__indicator--one {
    align-self: stretch;
    width: 1px;
    background-color: rgb(204, 204, 204);
    margin-bottom: 8px;
    margin-top: 8px;
    box-sizing: border-box;
    visibility: hidden;
}

.pincod__indicator--two {
    display: flex;
    transition: color 150ms ease 0s;
    color: white;
    padding: 8px;
    box-sizing: border-box;
}

.pincod__stroke--two {
    text-align: left;
    margin-top: 10px;
    grid-column: span 6 / span 6;
}
.pincod__wrapper {
    -moz-column-gap: 2rem;
    column-gap: 2rem;
    flex-wrap: wrap;
    flex-direction: row;
    display: flex;
}
.pincod__wrapper--initial {
    flex: 0 1 auto;
    min-width: 350px;
}
.pincod__initial {
    justify-content: flex-start;
    align-items: center;
    display: flex;
    margin-top: 25px;
}
.pincod__initial-subscribe {
    border: 2px solid #fff;
    height: 25px;
    width: 25px;
    min-width: 25px;
    cursor: pointer;
}

.pincod__initial--sub {
    border: 4px solid #000;
    height: 100%;
    width: 100%;
    background-color: #fff;
    display: none; /* Начально скрыть элемент */
}

.pincod__initial--s {
    border: 4px solid #000;
    height: 100%;
    width: 100%;
    background-color: #fff;
    display: none; /* Начально скрыть элемент */
}

.pincod__subscribe {
    opacity: 1;
    color: rgb(255 255 255);
    letter-spacing: 0.1em;
    line-height: 13px;
    font-size: 13px;
    font-family: GTAmericaStandardRegular;
    cursor: pointer;
    margin-left: 20px;
}


.pincod__link {
    border-bottom: 1px solid #fff;
    padding-bottom: 3px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    cursor: pointer;
}

.pincod__paragraph {
    line-height: 20px;
    text-transform: uppercase;
    font-size: 12px;
    font-family: GTAmericaMonoMedium;
    padding-top: 5px;
    height: 20px;
}

.pincod__column--bottom {
    margin-top: 20px;
    grid-column: span 6 / span 6;
}
.pincod__container--bottom {
    border: 1px solid #333;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    display: flex;
}
.pincod__leftitem--bottom {
    opacity: 1;
    color: rgb(0, 0, 0);
    text-align: center;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-top: auto;
    margin-bottom: auto;
    opacity: 1;
    background-color: rgb(255, 255, 255);
    cursor: pointer;
    flex: 1 1 auto;
    position: relative;
}
.pincod__title--bottom {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    letter-spacing: 0.08em;
    line-height: 20px;
    text-transform: uppercase;
    font-size: 12px;
    font-family: GTAmericaMonoMedium;

    @include mobile {
        font-size: 10px;
    }
}
.pincod__paragraph--bottom {
    margin-top: 6px;
}
.pincod__symbol--bottom {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 95%;
    text-transform: uppercase;
    font-size: 30px;
    font-family: GTAmericaStandardBold;

    @include mobile {
        font-size: 20px;
    }
}
.pincod__cash--bottom {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 95%;
    text-transform: uppercase;
    font-size: 30px;
    font-family: GTAmericaStandardBold;

    @include mobile {
        font-size: 20px;
    }
}
.pincod__curs--month {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 100%;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    font-size: 12px;
    font-family: GTAmericaMonoMedium;
}
.pincod__rightitem--bottom {
    opacity: 1;
    color: rgb(255, 255, 255);
    text-align: center;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-top: auto;
    margin-bottom: auto;
    opacity: 1;
    background-color: rgb(2, 2, 2);
    cursor: pointer;
    flex: 1 1 auto;
    position: relative;
}
.pincod__image {
    width: 80px;
    display: block;
    top: -20px;
    left: 91%;
    position: absolute;

    @include small-tablet {
        top: -40px;
        left: 75%;
    }

    @include mobile {
        left: 55%;
        top: -60px;
    }
}
.pincod__text--one {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    opacity: 1;
    color: rgb(0 0 0);
    line-height: 100%;
    text-transform: uppercase;
    font-size: 25px;
    font-family: GTAmericaBlack;
    rotate: 15deg;
    width: 80px;
    display: block;
    top: 5px;
    left: 92%;
    position: absolute;

    @include small-tablet {
        left: 78%;
        top: -15px;
    }

    @include mobile {
        font-size: 18px;
        left: 58%;
        top: -30px;
    }
}
.pincod__text--two {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    opacity: 1;
    color: rgb(0 0 0);
    line-height: 100%;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    font-size: 9px;
    font-family: GTAmericaBlack;
    rotate: 15deg;
    width: 80px;
    display: block;
    top: 30px;
    left: 91%;
    position: absolute;

    @include small-tablet {
        top: 10px;
        left: 75%;
    }

    @include mobile {
        left: 55%;
        top: -10px;
    }
}
.pincod__stroke--three {
    grid-column: span 6 / span 6;
}
.pincod__form--one {
    vertical-align: middle;
    margin-top: 25px;
}
.pincod__subform {
    column-gap: 1rem;
    flex-direction: row;
    display: flex;
}

.pincod__stroke {
    flex: 1 1 0%;
}

.pincod__button {
    flex: none;
}
.pincod__button--one {
    font-family: GTAmericaMonoMedium;
    align-items: center;
    border-radius: 25px;
    color: #fff;
    text-transform: uppercase;
    height: 48px;
    font-size: 12px;
    border: 1px solid #fff;
    letter-spacing: 0.12em;
    padding-left: 25px;
    padding-right: 25px;
    width: auto;
    background-color: transparent;
    background-image: none;
    cursor: pointer;
    transition:
        background-color 0.5s ease,
        color 0.5s ease;
}

.pincod__button--one:hover {
    background-color: rgb(255, 255, 255); /* добавлено */
    color: rgb(0, 0, 0); /* добавлено */
}

.pincod__stroke--four {
    text-align: center;
    margin-top: 10px;
    grid-column: span 6 / span 6;
}
.pincod__bta {
    font-family: GTAmericaMonoMedium;
    align-items: center;
    border-radius: 25px;
    color: #fff;
    text-transform: uppercase;
    height: 48px;
    font-size: 12px;
    border: 1px solid #fff;
    letter-spacing: 0.12em;
    padding-left: 25px;
    padding-right: 25px;
    width: auto;
    background-color: transparent;
    background-image: none;
    cursor: pointer;
    margin-top: 25px;
    transition:
        background-color 0.5s ease,
        color 0.5s ease;
}

.pincod__bta:hover {
    background-color: rgb(255, 255, 255); /* добавлено */
    color: rgb(0, 0, 0); /* добавлено */
}
.pincod__stroke--five {
    grid-column: span 6 / span 6;
    text-align: center;
}
.pincod__notice {
    font-family: GTAmericaStandardRegular;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    opacity: 0.4;
    color: rgb(255 255 255);
    font-size: 13px;
    line-height: 140%;
}

.pincod__column--right {
    grid-column: span 1 / span 1;

    @include tablet {
        display: none;
    }
}
.pincod__right--contaienr {
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
}
.pincod__right--content {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    width: 100%;
    min-height: 20px;
    min-width: 20px;
    overflow: hidden;
}
.pincod__content--image {
    opacity: 1;
    transition: opacity 0.2s ease-in-out 0s;
    object-fit: cover;
    object-position: center center;
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0px;
    top: 0px;
}
.pincod__image--size {
    width: 100%;
    padding-top: 125.1%;
    pointer-events: none;
    font-size: 0px;
}
