.email {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    padding: 20px 20px 40px;
    min-height: 100px;
    width: 100vw;
    margin-left: calc(50% - 50vw);
    background-color: rgb(236, 184, 160);
}

.email__container {
    width: 100%;
    align-self: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    box-sizing: border-box;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    margin-left: auto;
    margin-right: auto;
}

.email__content {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 30px;
    margin-bottom: 30px;
}

.email__subcontent {
    display: flex;
    flex-direction: row;

    @include middle-tablet {
        flex-direction: column;
    }
}

.email__column--one {
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    line-height: normal;
    width: calc(50% - 10px);
    margin-left: 0px;

    @include middle-tablet {
        width: 100%;
        margin-left: 0;
    }
}

.email__column--two {
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    line-height: normal;
    width: calc(50% - 10px);
    margin-left: 20px;

    @include middle-tablet {
        width: 100%;
        margin-left: 0;
    }
}

.email__item {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    height: auto;
    margin-bottom: 50px;
}

.email__title {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    line-height: 34px;
    height: auto;
    color: rgb(0, 0, 0);
    font-family: HelveticaNeue-Bold, sans-serif;
    font-size: 36px;
    margin-bottom: 10px;
}

.email__description {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    line-height: 24px;
    height: auto;
    color: rgb(0, 0, 0);
    font-family: HelveticaNeue-Light, sans-serif;
    font-size: 18px;
    max-width: 400px;
}

.email__sub-title {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    line-height: 22px;
    height: auto;
    color: rgb(0, 0, 0);
    font-family: HelveticaNeue-Bold, sans-serif;
    font-size: 24px;
    margin-bottom: 5px;
    margin-top: 30px;
}

.email__button {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 15px;
    appearance: none;
    padding: 15px 35px;
    background-color: rgba(0, 0, 0, 0);
    color: rgb(0, 0, 0);
    border-radius: 100px;
    text-align: center;
    cursor: pointer;
    border-width: 1px;
    border-style: solid;
    backdrop-filter: blur(10px);
    text-transform: uppercase;
    font-family: SohneMono, sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
    width: auto;
    align-self: center;
    margin-right: auto;
    border-color: rgb(0, 0, 0);
    transition: background-color 0.5s ease, color 0.5s ease; /* добавлено */
}

.email__button:hover {
    background-color: white; /* добавлено */
    color: black; /* добавлено */
}


