.identifi {
  display: flex;
  flex-direction: column;
  position: relative;
  flex-shrink: 0;
  box-sizing: border-box;
  margin-top: 0px;
  padding: 0px;
  min-height: 100vh;
  width: 100vw;
  background-color: rgb(0, 0, 0);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  border-width: 0px;
  border-top-style: solid;
  border-bottom-style: solid;
  border-left-style: initial;
  border-left-color: initial;
  border-right-style: initial;
  border-right-color: initial;

  @include tablet{
    margin: 0;
  }
}

.identifi__container {
  width: 100%;
  align-self: stretch;
  -webkit-box-flex: 1;
  flex-grow: 1;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  -webkit-box-align: stretch;
  align-items: stretch;
  margin-left: auto;
  margin-right: auto;
  transition: transform 0.5s ease-in-out; /* Анимация будет применяться к свойству transform */

}



/* Остальные стили остаются без изменений */
.identifi__content {
display: flex;
flex-direction: column;
position: relative;
flex-shrink: 0;
box-sizing: border-box;
margin-top: 0px;
background-image: url(../../img/svg/kun3.png);
background-repeat: no-repeat;
background-position: center center;
background-size: cover;
border-top-style: solid;
border-bottom-style: solid;
border-left: 0px;
border-right: 0px;
min-height: 120vh;
width: 100%;
margin-left: auto;
margin-right: auto;
}


.identifi__title {
  display: flex;
  flex-direction: column;
  position: relative;
  flex-shrink: 0;
  box-sizing: border-box;
  margin-top: 200px;
  line-height: 0.9em;
  height: auto;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  font-family: America-Compressed-Black, sans-serif;
  color: rgb(205, 205, 205);
  font-size: 80px;
  margin-bottom: 20px;
  line-height: 100%;


  @include tablet{
    font-size: 70px ;

  }

  @include mobile {
    font-size: 50px;
    
  }
}

.identifi__nav-link {
  /* Ваши текущие стили для ссылок */
  transition: color 0.3s, transform 0.3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.identifi__nav-link:hover {
  color: white;
  transform: scale(1.1);
}

.identifi__nav-link:active {
  transform: scale(0.9); /* Уменьшаем масштаб при нажатии */
}




