.slick-slider {
    overflow: hidden;
    -webkit-tap-highlight-color: transparent;
    touch-action: pan-y;
    position: relative;
    list-style: none;
    padding: 0;
    margin-left: auto;
        margin-right: auto;
}

.slick-list {
    overflow: visible;
    position: relative;
    display: block;
    margin: 0;
    padding: 0;
    transform: translate3d(0, 0, 0);
}

.slick-track {
    overflow: visible;
    position: relative;
    top: 0;
    left: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.slick-initialized .slick-slide {
    display: block;
}

.slick-slide {
    width: 272px;
    float: left;
    height: 100%;
    min-height: 1px;
    padding: 5px;
    overflow: visible;
    display: block;
}