.footer {
    display: flex;
    width: 100vw;
    box-sizing: border-box;
    width: 100%;
    background-color: #142936;
    padding-top: 100px;
    padding-bottom: 20px;

    @include small-tablet {
        padding-top: 50px;
    }

    @include mobile {
        padding-top: 50px;
    }
}

.footer__container {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 0 30px;
}

.footer__column--main {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: auto;
    width: 100%;
    gap: 20px;

    @include mobile {
        grid-template-columns: repeat(3, 1fr);
        margin-bottom: 30px;
        
    }
}

.footer__column--one,
.footer__column--two{
    border-right: 1px solid black;

    @include mobile {
        border-right: 0.5px solid black;
    }
}

.footer__column--three {
    border-right: 1px solid black;

    @include mobile {
        border-right: none; /* Отключить бордер для третьего столбца на мобильных экранах */
    }
}

.footer__content {
    display: flex;
    flex-direction: column;
}

.footer__item-title {
    margin-bottom: 2.5em;
    font-weight: bold;
    color: antiquewhite;

    @include small-tablet {
        font-size: 11px;
    }

    @include mobile {
        font-size: 10px;
    }

    @include mini-mobile {
        font-size: 8px;
    }
}

.footer__item-text {
    margin: 0 0 10px 0;
    font-size: 14px;
    color: rgb(255, 255, 255);
    cursor: pointer;
    transition: all 0.5s cubic-bezier(0.37, 0.01, 0, 0.98) 0s;

    @include small-tablet {
        font-size: 11px;
    }

    @include mobile {
        font-size: 10px;
    }

    @include mini-mobile {
        font-size: 7.5px;
        line-height: 2em;
    }
}

.footer__item-text:hover {
    color: rgb(0, 145, 255);
}

.footer__item-link {
    color: inherit;
    text-decoration: none;
}

.footer__signup {
    grid-row: 1 / 4;
    grid-column: 4;

    @include mobile {
       margin-bottom: 10px;
        grid-column: 1 / -1; /* Занимает всю ширину на мобильных экранах */
    }
}

.footer__title {
    margin-bottom: 10px;
    font-weight: bold;
    color: antiquewhite;;

    @include small-tablet {
        font-size: 13px;
    }

    @include mobile {
        font-size: 10px;
    }
}

.footer__email-container {
    position: relative; /* This allows you to position the button inside */
    display: flex;
    align-items: center;
}

.footer__email-input {
    width: 100%;
    border-radius: 20px; /* This rounds the corners */
    margin-top: 1em;
    border: 1px solid grey;
    padding: 5px 40px 5px 10px; /* This provides space for the text and the button */
    box-sizing: border-box; /* This ensures the padding is included in the width */

    @include mobile {
        font-size: 8px;
    }
}

.footer__submit-button {
    position: absolute; /* This positions the button inside the input field */
    right: 10px; /* This positions the button to the right */
    top: 20px;
    border: none; /* This removes the default button border */
    background: none; /* This removes the default button background */
    cursor: pointer; /* This changes the cursor when hovering over the button */

    @include mobile {
        top: 11px;
    }
}

.footer__submit-button img {
    width: 20px;
    height: 20px;
}

.footer__copyrights {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    border-top: 1px solid black;
    padding-top: 20px;
}

.footer__copyrights a {
    color: beige;
    text-decoration: none;
    margin: 0 5px;
}
