.history {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    padding: 0px;
    min-height: 100vh;
    width: 100vw;
    background-color: rgb(0, 0, 0);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    border-width: 0px;
    border-top-style: solid;
    border-bottom-style: solid;
    border-left-style: initial;
    border-left-color: initial;
    border-right-style: initial;
    border-right-color: initial;
  
    @include tablet{
      margin: 0;
    }
  }
  
  .history__container {
    width: 100%;
    align-self: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    margin-left: auto;
    margin-right: auto;
    transition: transform 0.5s ease-in-out; /* Анимация будет применяться к свойству transform */
  
  }
  
  
  
  /* Остальные стили остаются без изменений */
  .history__content {
  display: flex;
  flex-direction: column;
  position: relative;
  flex-shrink: 0;
  box-sizing: border-box;
  margin-top: 0px;
  background-color: #1a2b36;
  min-height: 100vh;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  }
  
  
  .history__title {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 200px;
    line-height: 0.9em;
    height: auto;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    font-family: America-Compressed-Black, sans-serif;
    color: rgb(255, 255, 255);
    font-size: 60px;
    font-weight: bold;
    margin-bottom: 20px;
  
    @include tablet{
      font-size: 45px ;
      line-height: 100%;
    }
  }

  .history__description {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 20px;
    line-height: 100%;
    height: auto;
    text-align: center;
    color: rgb(255, 255, 255);
    font-size: 36px;
    padding-left: 0px;
    padding-right: 0px;
    font-family: HelveticaNeue-Light, sans-serif;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 30px;

    @include mobile {
      font-size: 19px;
      width: 90%;
    }
    
}

.history__description-text {
    /* Ваши стили для текста описания */
}


  .history__bta {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 40px;
    appearance: none;
    padding: 15px 35px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border-radius: 100px;
    text-align: center;
    cursor: pointer;
    border-width: 1px;
    border-style: solid;
    backdrop-filter: blur(10px);
    text-transform: uppercase;
    font-family: SohneMono, sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
    width: auto;
    align-self: center;
    line-height: normal;
    margin-bottom: 40px;
    transition: background-color 0.5s ease, color 0.5s ease; /* добавлено */
}

.history__bta:hover {
    background-color: white; /* добавлено */
    color: black; /* добавлено */
}

  
  
  
  
  
  
  