.header {
  padding: 0, 0;
  background-color: transparent;
}

.header--position {
  left: 0;
  top: 0px;
  position: absolute;
  width: 100%;
  z-index: 10;
}

.header__link {
  cursor: default;
  display: flex;
  pointer-events: none; /* Отключаем обработку событий мыши для .header__link */
}

.header__container {
  display: grid;
  gap: 0.5rem;
}

.header__logo {
  transition-duration: 0.2s;
  margin: 0.75rem;
  grid-column-start: 1;
  cursor: default;
}

.header.black {
}

.header img {
  height: 150px;
  /* Set a fixed height for the logo */
  width: auto;
  cursor: pointer;
  /* Let the width adjust automatically */
  max-height: 100%;
  /* Ensure the logo does not exceed its parent container */
  max-width: 100%;
  /* Ensure the logo does not exceed its parent container */
  pointer-events: auto; /* Включаем обработку событий мыши только для изображения */
}

.header__bottom {
  display: none; /* Скрываем блок на десктопе */

  @include tablet {
    display: block;
    position: fixed;
    width: 100%;
    z-index: 9999;
    bottom: 0;
    background-color: #c89a9a;
  }

  @include tablet {
    .header__bottom--list {
    }

    .header__bottom--item {
      display: grid;
      grid-auto-flow: column;
      padding: 0.75rem;
    }

    .header__bottom--link {
      color: rgb(0, 0, 0);
      font-family: SohneMono, sans-serif;
      justify-content: center;
      place-items: center;
      flex-direction: column;
      height: 100%;
      display: flex;
      position: relative;
    }

    .header__bottom--image {
      cursor: pointer;
      max-width: 100%;
      height: auto;
      color: black;
      @include mini-mobile {
        width: 30px; /* Установка желаемой ширины */
        height: 30px; /* Установка желаемой высоты */
      }
    }

    .header__bottom--paragraph {
      margin-top: calc(0.5rem * calc(1 - 0));
      margin-bottom: calc(0.5rem * 0);
      color: rgb(255 255 255);
      letter-spacing: 0.025em;
      font-size: 0.75rem;
      line-height: 1rem;
      gap: 0.5rem;
      display: flex;
      position: relative;
    }
  }
}
