.place__section {
    position: relative;
    padding-top: 100px;
    display: block;
    background-color: antiquewhite;
}

.place__container {
    max-width: 1220px;
    width: 100%;
    margin: 0 auto;
    justify-content: center;
    text-align: center;
    display: flex;
    flex-wrap: wrap;
}

.place__content {
    position: relative;
    margin-left: 0.75%;
    margin-right: 0.75%;
    width: 98.5%;
}

.place__image {
    display: inline-block;
    width: 21%;
    margin: 0 3px;
    max-width: 80px;
}

.place__content--card {
    width: 90%;
    margin: 0 auto;
    max-width: 560px;
}

.place__desktop--subtitle {
    display: block;
    letter-spacing: -0.5px;
    font-weight: 600;
    line-height: 1;
    font-size: 30px;
    font-family: Souvenir, serif;
}

.place__desktop--title {
    margin-bottom: 20px;
    letter-spacing: -0.5px;
    line-height: 1;
    font-size: 56px;
    font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
    display: block;
    font-weight: bold;
    position: relative; /* Добавьте эту строку */
}

.place__desktop--title::after {
    content: "";
    position: absolute;
    top: 100%;
    right: -2px;
    left: -2px;
    height: 10px;
    margin-top: 2px;
    background-color: #ffd400;
    z-index: 0;
    border-radius: 22px;
}

.place__desktop--discription {
    display: block;
}

.place__slider {
    padding-left: 40px;
    padding-right: 40px;
    max-width: 1220px;
    width: 100%;
    margin: 0 auto;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;

    @include small-tablet {
        padding-left: 0;
        display: block;
        margin-top: 10px;
      

    }
}

.place__margin-top {
    margin-top: 40px;

    @include small-tablet {
    }
}

.place__slide {
    background: #7c7575;
    padding: 18px 16px;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    transition: all 0.3s ease-in-out;
    display: flex;
    margin-bottom: 1.5%;
    position: relative;
    margin-left: 0.75%;
    margin-right: 0.75%;
    width: 23.5%;
    color: black;
    text-decoration: none;
    cursor: pointer;
    


    @include small-tablet {
        white-space: nowrap;
        margin-left: auto;
        margin-right: auto;

    }
}

.place__slide:hover {
    background: #fff;
}

.place__slide:hover .card__image--car {
    filter: brightness(0) invert(1); /* Изменяем цвет на белый при ховере на .place__card */
    transform: translateX(-5px);
    animation: shake 3s infinite;
}

.place__slide:hover .card__button--link {
    background-color: #248742; /* Изменяем фон на зеленый */
    color: #fff; /* Изменяем цвет текста на белый */
}

.place__slide:hover .lazy.image.lazy-loaded {
    transform: scale(1.05); /* Увеличиваем изображение при ховере */
    transition: transform 0.3s ease-in-out; /* Добавляем плавный переход для анимации */
}

@keyframes shake {
    0% {
        transform: translateX(-5px);
    }
    50% {
        transform: translateX(5px);
    }
    100% {
        transform: translateX(-5px);
    }
}

.place__card--image {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.card__image {
    background-color: #fafafb;
    width: 100%;
    height: 0;
    padding-bottom: 85%;
    overflow: hidden;
    position: relative;
    display: block;
}

.image {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    top: 0;
    left: 0;
    transform: scale(1);
    transition: all 0.3s ease-in-out;
}

.card__title--one {
    margin-top: 14px;
    margin-bottom: 6px;
    font-size: 16px;
    line-height: 1.2;
    font-family: Axiforma, Helvetica, Arial, sans-serif;
    text-align: center;
    display: block;
}

.card__title--two {
    margin-bottom: 10px;
    letter-spacing: -0.5px;
    font-weight: 400;
    font-size: 12px;
    font-family: Axiforma, Helvetica, Arial, sans-serif;
    text-align: center;
    line-height: 1.2;
    display: block;
}

.card__button--link {
    margin-top: auto;
    width: 100%;
    background-color: #fff;
    border-radius: 46px;
    border: 1px solid #248742;
    display: block;
    padding: 7px 10px 5px 10px;
    transition: all 0.3s ease-in-out;
    color: #248742;
    font-weight: 700;
    font-size: 14px;
    text-align: center;
    position: relative;
}

.card__image--car {
    vertical-align: middle;
    margin-bottom: 4px;
    margin-right: 7px;
    transition:
        filter 0.3s ease-in-out,
        transform 3s ease-in-out;
    animation-name: none;
    width: 24px;
    display: inline-block;
}

/* Добавляем ключевую кадровую анимацию "shake" */

.desktop__text {
    display: inline;
}

.mobile__text {
    display: none;
}

.place__mobile--container {
    max-width: 1220px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
}

.place__mobile--discription {
    display: none;
    padding: 0 20px;
    line-height: 1.3;
    font-size: 14px;
    text-align: center;
    position: relative;
    margin-left: 0.75%;
    margin-right: 0.75%;
    width: 98.5%;
}

.place__container--button {
    margin-top: 50px;
    max-width: 1220px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 5rem;
}

.place__paragraph {
    text-align: center;
    position: relative;
    margin-left: 0.75%;
    margin-right: 0.75%;
    width: 98.5%;
    display: block;
}

.place__text {
    font-weight: 700;
    line-height: 2;
    display: block;
}

.place__link {
    transition: all 0.3s ease-in;
    color: #248742;
    text-decoration: none;
    cursor: pointer;
}

.place__link:hover .arrow {
    margin-left: 10px; /* Сдвигаем .arrow вправо при наведении на .place__link */
    transition: all 0.3s ease-in-out; /* Добавляем плавный переход для анимации */
}

.arrow {
    display: inline-block;
    vertical-align: middle;
    margin-left: 5px;
    margin-right: 10px;
    margin-bottom: 2px;
    transition: all 0.2s ease-in-out;
}

