.gift {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    padding: 0px;
    min-height: 100px;
    background-color: rgb(239, 199, 180);
    width: 100vw;
    margin-left: calc(50% - 50vw);
}

.gift__container {
    width: 100%;
    align-self: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    margin-left: auto;
    margin-right: auto;
}

.gift__row {
    display: flex;
    flex-direction: row;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;

    @include middle-tablet {
        flex-direction: column;
    }
}

.gift__column--left {
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    line-height: normal;
    width: calc(50% - 0px);
    margin-left: 0px;

    @include middle-tablet {
        display: none;
    }
}

.gift__column--right {
    display: flex;
    flex-direction: column;
    -webkit-box-align: stretch;
    align-items: stretch;
    line-height: normal;
    width: calc(50% - 0px);
    margin-left: 0px;

    @include middle-tablet {
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
}

.gift__image-container--one {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    height: 100%;
    background-image: url(../../img/svg/podarok.jpeg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}


.gift__image-decoration {
    /* Стили для декоративного элемента изображения внутри левой колонки в секции "Gift" */
}

.gift__content {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    height: 90vh;
    padding-top: 100px;
    padding-bottom: 30px;
    font-size: 40px;
    line-height: 35px;
    -webkit-box-pack: center;
    justify-content: center;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    background-color: rgb(249 237 218);
    color: rgb(0, 0, 0);
}

.gift__title--one {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    line-height: 50px;
    height: auto;
    font-family: SohneBreit-fettKursiv, sans-serif;
    text-align: center;
    font-size: 55px;
    padding-left: 30px;
    padding-right: 30px;
    margin-bottom: 5px;
    max-width: 550px;
    margin-left: auto;
    margin-right: auto;

    @include mobile {
        font-size: 35px;
        line-height: 100%;
    }
}

.gift__title--two {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    line-height: 65px;
    height: auto;
    font-family: SohneBreit-ExtrafettKursiv, sans-serif;
    text-align: center;
    font-size: 60px;
    padding-left: 30px;
    padding-right: 30px;
    margin-bottom: 20px;
    max-width: 700px;
    font-weight: bold;
    margin-left: auto;
    margin-right: auto;

    @include mobile {
        font-size: 40px;
        line-height: 100%;
    }
}

.gift__title-text {
    /* Стили для текста заголовков внутри правой колонки в секции "Gift" */
}

.gift__description {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 20px;
    line-height: 20px;
    height: auto;
    text-align: center;
    font-size: 16px;
    padding-left: 0px;
    padding-right: 0px;
    font-family: HelveticaNeue-Bold, sans-serif;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 30px;
    border-width: 1px 0px 0px;
    border-top-style: solid;
    border-bottom-style: initial;
    border-bottom-color: initial;
    border-left-style: initial;
    border-left-color: initial;
    border-right-style: initial;
    border-right-color: initial;

    @include mobile {
        width: 95%;
        line-height: 100%;
       
    }
}

.gift__description-text {
    /* Стили для текста описания внутри правой колонки в секции "Gift" */
}

.gift__cta {
    display: flex;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 35px;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    margin-bottom: 20px;

    @include mobile {
        flex-direction: column;
    }
}

.gift__cta-button--one {
    display: flex;
    flex-direction: column;
    position: relative;
    flex-shrink: 0;
    box-sizing: border-box;
    margin-top: 0px;
    appearance: none;
    padding: 15px 35px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border-radius: 100px;
    text-align: center;
    cursor: pointer;
    border-width: 1px;
    border-style: solid;
    backdrop-filter: blur(10px);
    text-transform: uppercase;
    font-family: SohneMono, sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
    width: auto;
    align-self: center;
    margin-right: 25px;
    margin-bottom: 35px;
    line-height: normal;
    transition: all 0.5s cubic-bezier(0.37, 0.01, 0, 0.98) 0s;

    @include mobile {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;

    }
}

.panch {
    color: rgb(131, 60, 32);
}

.gift__cta-button--one:hover {
    background-color: rgb(0, 0, 0);
    color: rgb(255, 255, 255);
}

