.heder {
    padding: 0, 0;
    background-color: rgb(239, 199, 180);
    }
    
    .heder__container {
    display: grid;
    gap: 0.5rem;
    }
    
    .heder__logo {
    transition-duration: 0.2s;
    margin: 0.75rem;
    grid-column-start: 1;
    cursor: default;
    }
    
    .heder.black {
    filter: brightness(0%);
    }
    
    .heder img {
    height: 150px;
    /* Set a fixed height for the logo */
    width: auto;
    cursor: pointer;
    /* Let the width adjust automatically */
    max-height: 100%;
    /* Ensure the logo does not exceed its parent container */
    max-width: 100%;
    /* Ensure the logo does not exceed its parent container */
    }
    
    .nav__item {
    gap: 60px;
    }
    
    .nav__list {
    display: grid;
    grid-template-columns: repeat(5, minmax(0, 1fr));
    margin: 0.75rem;
    grid-column-end: 10;
    grid-column-start: 5;
    list-style: none;
    padding: 0;
    
    @include tablet {
    display: none;
    }
    }
    
    .nav__link {
    display: flex;
    position: relative;
    font-family: SohneMono, sans-serif;
    justify-content: center;
    place-items: center;
    flex-direction: column;
    transition: color 0.3s ease-in-out;
    height: 100%;
    color: rgb(255 255 255);
    cursor: default;
    }
    
    .nav__link:focus {
    outline: none;
    }
    
    .nav__link:hover {
    color: rgb(238 117 41);
    }
    
    .nav__link:active {
    color: var(--active-color);
    }
    
    .nav__link.focus-visible {
    outline: 1px solid var(--active-color);
    outline-offset: 3px;
    }
    
    .heder__image {
        color: rgb(0, 0, 0);
        cursor: pointer;
        transition: color 0.3s; /* Добавляем плавность с изменением цвета за 0.3 секунды */
     }
     
     .heder__image:hover {
        color: antiquewhite; /* Замените "ваш-новый-цвет" на желаемый цвет при наведении */
     }
     
    
    .heder__paragraph {
    display: flex;
    position: relative;
    margin-top: calc(0.5rem * calc(1 - 0));
    margin-bottom: calc(0.5rem * 0);
    color: rgb(255 255 255);
    letter-spacing: 0.025em;
    font-size: 0.75rem;
    line-height: 1rem;
    gap: 0.5rem;
    }
    
    .column-three-content {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-column-end: 13;
    grid-column-start: 11;
    gap: 10px;
    flex-wrap: nowrap;

    @include tablet {
        display: none;
    }
    }
    
    .header__login {
    display: flex;
    justify-content: center;
    place-items: center;
    }
    
    .header__login a {
    opacity: 1;
    color: rgb(14, 14, 14);
    text-decoration: none;
    padding: 1rem;
    border: none;
    font-size: 0.875rem;
    line-height: 1.25rem;
    font-family: SohneMono, sans-serif;
    cursor: pointer;
    position: relative;
    background-color: transparent;
    background-image: none;
    }
    
    .header__login a::after {
    content: "";
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: rgb(238 117 41); /* underline color */
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
    }
    
    .header__login a:hover::after {
    transform: scaleX(1);
    transform-origin: bottom left;
    }
    
    .header__current--time {
    display: flex;
    justify-content: center;
    padding-left: 15px;
    padding-right: 15px;
    place-items: center;
    background-color: rgb(255 255 255);
    }
    
    .header__current--text {
    font-style: italic;
    font-weight: 700;
    font-family: SohneBreit, sans-serif;
    cursor: pointer;
    }
    
    .modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    padding-top: 2rem;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(181, 224, 165);
    align-items: center;
    justify-content: center;
    }
    
    .modal-content {
    background-color: #ecec8d;
    margin: auto;
    padding: 20px;
    border: 1px solid #00000021; /* solid black border */
    width: 27%;
    min-height: 30vh; /* Minimum height */
    max-height: 90vh; /* Maximum height */
    overflow: auto; /* Add a scrollbar if necessary */
    }
    
    .lotos-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    padding-left: 13px;
    }
    
    #lotos {
    /* You can set the width and height as per your logo size. */
    width: 70px;
    height: 70px;
    }
    
    .form-title {
    text-align: center;
    margin-bottom: 12px;
    font-size: 20px;
    }
    
    .input-group {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative; /* Добавлено */
    margin-bottom: 10px;
    }
    
    .input-group label {
    margin-bottom: 5px;
    }
    
    .input-group input {
    width: 100%;
    height: 30px;
    border-radius: 5px;
    padding-left: 15px;
    padding-right: 50px; /* Добавлено, чтобы предотвратить наложение текста на "Show" */
    }
    
    .input-group input:focus {
    outline:
    none;
    box-shadow: 0 0 10px #9ecaed;
    }
    
    .password-toggle {
    /* Добавлено */
    position: absolute;
    right: 10px;
    top: 34px;
    cursor: pointer;
    font-size: 14px;
    }
    
    .forgot-password-link {
    background: none !important;
    border: none;
    padding: 0 !important;
    font: inherit;
    cursor: pointer;
    text-decoration: none;
    font-size: 14px;
    transition: all 0.3s ease-in-out;
    position: relative;
    overflow: hidden;
    }
    
    .forgot-password-link::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 1px;
    background: currentColor;
    transition: width 0.3s ease-in-out;
    }
    
    .forgot-password-link:hover::after {
    width: 100%;
    }
    
    .sign-in-button {
    width: 100%;
    height: 35px;
    border-radius: 5px;
    margin-bottom: 13px;
    margin-top: 15px; /* Отступ сверху для разделения кнопок */
    color: #ffffff; /* Белый цвет текста */
    background-color: #dacbcb; /* Серый цвет фона */
    border: none; /* Убираем рамку */
    cursor: pointer; /* Добавляем указатель курсора */
    }
    
    /* Стиль при наведении */
    .sign-in-button:hover {
    background-color: rgb(181, 224, 165); /* Желтый цвет фона при наведении */
    }
    
    /* Стиль при нажатии */
    .sign-in-button:active {
    background-color: rgb(181, 224, 165); /* Темно-желтый цвет фона при нажатии */
    }
    
    .or-line {
    position: relative;
    text-align: center;
    margin: 10px 0; /* Добавить отступы сверху и снизу, если необходимо */
    }
    
    .or-line::before,
    .or-line::after {
    content: "";
    position: absolute;
    top: 50%;
    width: 45%;
    border-bottom: 1px solid #000; /* измените цвет линии по желанию */
    }
    
    .or-line::before {
    left: 0;
    }
    
    .or-line::after {
    right: 0;
    }
    
    .or-text {
    padding: 0 10px; /* отступы вокруг текста "OR" */
    font-size: 14px; /* Уменьшение размера шрифта */
    }
    
    .social-login {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #635f5f;
    color: white;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
    cursor: pointer;
    transition: all 0.3s;
    width: 100%;
    height: 40px;
    }
    
    .social-login img {
    flex: 0;
    }
    
    .social-icon {
    width: 15px;
    height: 15px;
    }
    
    .social-text {
    flex: 1; /* Добавлено */
    margin-right: 0.7rem;
    text-align: center; /* Добавлено */
    font-size: 0.8em; /* уменьшение размера шрифта */
    }
    
    .registration-prompt {
    text-align: center; /* Центрирование текста */
    color: gray; /* Серый цвет текста */
    position: fixed; /* Фиксированное позиционирование */
    bottom: 0; /* Прижато к нижней границе окна браузера */
    width: 100%; /* Ширина равна ширине окна браузера */
    padding: 20px 0; /* Отступы сверху и снизу */
    }
    
    .registration-prompt a {
    text-decoration: underline; /* Добавление подчеркивания к ссылке */
    }
    
    .close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    transition: transform 0.5s; /* Добавлено: обеспечивает плавный переход анимации */
    }
    
    .close:hover,
    .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
    transform: rotate(360deg); /* Добавлено: применяет анимацию поворота */
    }
    
    .header__bottom {
    display: none; /* Скрываем блок на десктопе */
    
    @include tablet {
    display: block;
    position: fixed;
    width: 100%;
    z-index: 9999;
    bottom: 0;
    background-color: #c89a9a;
    }
    
    @include tablet {
    .header__bottom--list {
    }
    
    .header__bottom--item {
    display: grid;
    grid-auto-flow: column;
    padding: 0.75rem;
    }
    
    .header__bottom--link {
    color: rgb(255 255 255);
    font-family: SohneMono, sans-serif;
    justify-content: center;
    place-items: center;
    flex-direction: column;
    height: 100%;
    display: flex;
    position: relative;
    }
    
    .header__bottom--image {
    filter: invert(92%) sepia(94%) saturate(31%) hue-rotate(257deg) brightness(107%) contrast(100%);
    cursor: pointer;
    max-width: 100%;
    height: auto;
    
    @include mini-mobile {
    width: 30px; /* Установка желаемой ширины */
    height: 30px; /* Установка желаемой высоты */
    }
    }
    
    .header__bottom--paragraph {
    margin-top: calc(0.5rem * calc(1 - 0));
    margin-bottom: calc(0.5rem * 0);
    color: rgb(255 255 255);
    letter-spacing: 0.025em;
    font-size: 0.75rem;
    line-height: 1rem;
    gap: 0.5rem;
    display: flex;
    position: relative;
    }
    }
    }
    