.trip__section {
    padding-bottom: 0;
    margin-bottom: -60px;
    background-color: #FFE9D3;
    background: linear-gradient(180deg, #FFE9D3 0%, #FFE9D3 96%, #fff 96%, #fff 100%);
    overflow: visible;
    position: relative;
    text-align: center;
    display: block;
    
}

/* Herobg Images */


/* Trip Container */
.trip__container {
    padding: 170px 0 120px 0;
    position: relative;
    width: 1140px;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    max-width: 92%;
}

/* Trip Content */
.trip__content {
    position: relative;
    margin-left: 0.75%;
    margin-right: 0.75%;
    width: 98.5%;
}

/* Trip Title */
.trip__title {
    text-align: center;
    max-width: 560px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
    letter-spacing: -0.5px;
    font-size: 52px;
    line-height: 1;
    font-family: Souvenir, serif;
    display: block;
    font-weight: bold;

    @include mobile {
        font-size: 35px;
    }
}

.adventure {
    position: relative; /* Необходимо для абсолютного позиционирования псевдоэлемента */
}

.adventure::after {
    content: ""; /* Обязательное свойство для псевдоэлемента */
    display: block; /* По умолчанию псевдоэлементы инлайновые, меняем на блочные */
    width: 100%; /* Ширина линии равна ширине родительского элемента */
    height: 5px; /* Толщина линии */
    background-color: green; /* Цвет линии */
    position: absolute; /* Абсолютное позиционирование псевдоэлемента */
    bottom: 0px; /* Поднимаем линию немного ниже текста */
    left: 0; /* Положение левого края псевдоэлемента */
}


/* Trip Subtitle (Desktop) */
.trip__dekstop--subtitle {
    max-width: 560px;
    margin-left: auto;
    font-family: Souvenir, serif;
    margin-right: auto;
    text-align: center;
    display: block;
    margin-top: 12px;
    margin-bottom: 24px;
    font-size: 20px;
    line-height: 1.55;
    font-weight: 600;
    display: none;

    @include mobile {
        display: none;
    }
}

/* Trip Widget */
.trip__widget {
    position: relative;
    transition-duration: 0.375s;
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 700px;
    display: none;

    @include mobile {
        display: none;
    }
}

/* Trip Search Title */
.trip__search--title {
    margin-bottom: 12px;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 0.1px;
    color: #000;
}

/* Trip Search Options Container */
.trip__search--options-container {
    text-align: left;
    
    @include small-tablet {
        text-align: center;
    }
}

/* Trip Search Options */
.trip__search--options {
    display: inline-flex;
    background-color: #F3F4F6;
    overflow: visible;
    flex-direction: row;
    z-index: 0;
    position: relative;
    vertical-align: top;
    border-radius: 4px;
    background: #FFF linear-gradient(rgba(255, 255, 255, 0.3) 10%, rgba(234, 234, 234, 0.3) 100%);
    box-shadow: 0 12px 24px -16px rgba(0, 0, 0, 0.5);
    
}

/* Trip Left Button Action */
.trip__left--button-action.active {
   
}

.trip__left--button-action {
    box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.05);
    border-top-right-radius: 8px;
    min-width: 144px;
    padding: 22px 28px;
    border-radius: 8px 8px 0px 0px;
    border: 1px solid #F3F4F6;
    background-color: #fff;
    position: relative;
    z-index: 2;
    display: flex;
    color: #292D31;
    flex-direction: row;
    gap: 1rem;

}

/* Trip Right Button Action */
.trip__right--button-action {
    box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.05);
    border-top-right-radius: 8px;
    min-width: 144px;
    padding: 23px 28px;
    border-radius: 8px 8px 0px 0px;
    border: 1px solid #F3F4F6;
    background-color: #fff;
    position: relative;
    z-index: 2;
    color: #292D31;
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

.trip__right--button-action.active {
    
}

/* Trip Forms */
.trip__search--forms {
    background: #fff;
    border: 1px solid #F3F4F6;
    border-radius: 8px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.05);
    height: 100%;
    width: 100%;
    z-index: 1;
    position: relative;
    margin-bottom: 38px;
}

/* Trip Form */
.trip__form {
    width: 100%;
    background: none;
    box-shadow: none;
    padding: 18px 20px 18px 12px;
    margin-top: 0;
    position: relative;
    left: auto;
    pointer-events: auto;
    visibility: visible;
    opacity: 1;
    transform: none;
}

/* Trip Form Grid */
.trip__form--grid {
    flex-wrap: nowrap;
    align-items: center;
    display: flex;

}

/* Trip Form Cell */
.trip__form--cell {
    flex-grow: 1;
    display: block;
}

.trip__cell {
    flex-wrap: nowrap;
    justify-content: space-between;
    display: flex;
}

.trip__cell--one {
    margin: 0;
    position: relative;
    width: 48.5%;
}

/* Trip Cell Subone */
.trip__cell--subone {
    width: 100%;
    border-right: none;
    border-left: none;
    padding: 0;
    border-radius: 4px 0 0 4px;
    position: relative;
    display: inline-block;
    vertical-align: top;
}

/* Trip Cell Label */
.trip__cell--label {
    opacity: 1;
    z-index: 3;
    padding-left: 35px;
    bottom: 8px;
    color: #8296AC;
    letter-spacing: 0.25px;
    font-size: 13px;
    line-height: 1.8;
    text-transform: none;
    position: absolute;
    left: 0;
    font-weight: 400;
    pointer-events: none;
    transition-duration: 0.25s;
    display: block;
}

/* Trip Cell Input */
.trip__cell--input {
    border-color: #8296AC;
    height: 44px;
    border: 1px solid #248742;
    border-radius: 8px;
    background-color: #F8F8F9;
    z-index: 2;
    position: relative;
    background-repeat: no-repeat;
    background-position: 10px 12px;
    box-shadow: 1px 1px 0px rgba(36, 135, 66, 0);
    padding-left: 34px;
    padding-top: 14px;
    margin-top: 0;
    color: #8296AC;
    font-size: 13px;
    line-height: 1.8;
    letter-spacing: 0.25px;
    width: 100%;
    margin-bottom: -1px;
    padding-right: 28px;
    box-sizing: border-box;
    transition-duration: 0.25s;
}

/* Trip Cell Autocomplete */
.trip__cell--autocomplete {
    width: 106%;
    left: -12px;
    top: -18px;
    padding-top: 0;
    z-index: 1;
    border: 1px solid transparent;
    overflow: visible;
    position: absolute;
    right: -24px;
    display: none;
    border-radius: 0 0 4px 4px;
    background-color: #fff;
    box-shadow: 0 8px 8px -4px rgba(0, 0, 0, 0.3);
    font-size: 14px;
    line-height: 28px;
    white-space: nowrap;
    text-align: left;
}

/* Trip Cell Error */
.trip__cell--error {
    bottom: -20px;
    left: 13px;
    z-index: 3;
    font-size: 10px;
    color: red;
    display: none;
    line-height: 20px;
    width: 100%;
    position: absolute;
    text-align: left;
}

/* Trip Form Button */
.trip__cell--button {
    flex-shrink: 1;
    display: block;
}

.trip--search__button {
    width: auto;
    min-width: 68px;
    height: 44px;
    background: #248742;
    padding: 13px;
    border-radius: 21px;
    display: inline-flex;
    flex-direction: row;
    margin-left: 10px;
    position: relative;
    z-index: 2;
    transition: all 0.3s ease-in;
    text-transform: uppercase;
    color: #fff;
    line-height: 1;
    font-size: 14px;
    font-family: Axiforma, Helvetica, Arial, sans-serif;
    cursor: pointer;
}
/* Trip Search Forms Two */
.trip__search--forms-two {
    width: 100%;
    background: none;
    box-shadow: none;
    padding: 18px 20px 18px 12px;
    margin-top: 0;
    position: relative;
    left: auto;
    pointer-events: auto;
    visibility: visible;
    opacity: 1;
    transform: none;
    z-index: 3;
    display: inline-block;
    vertical-align: top;
    display: none;
}

/* Trip From Two */
.trip__from--two {
    flex-wrap: nowrap;
    align-items: center;
    display: flex;
}

/* Trip Cell Two */
.trip__cell--two {
    flex-grow: 1;
    display: block;
}

/* Trip Group */
.trip__group {
    padding: 0;
    width: 100%;
    border-right: none;
    border-left: none;
    border-radius: 4px 0 0 4px;
    background: #fff;
    position: relative;
    display: inline-block;
    vertical-align: top;
    z-index: 2;
}

/* Trip Search Label */
.trip__search--label {
    line-height: 2.4;
    bottom: 16px;
    opacity: 1;
    z-index: 3;
    padding-left: 35px;
    color: #E12B39;
    letter-spacing: 0.25px;
    line-height: 2.4;
    font-size: 11px;
    text-transform: none;
    position: absolute;
    left: 0;
    font-weight: 400;
}

/* Trip Search Label Two */
.trip__search--label--two {
    display: none;
    bottom: 16px;
    opacity: 1;
    z-index: 3;
    padding-left: 35px;
    color: #E12B39;
    letter-spacing: 0.25px;
    line-height: 2.4;
    font-size: 11px;
    text-transform: none;
    position: absolute;
    left: 0;
    font-weight: 400;
}

/* Trip Input */
.trip__input {
    height: 44px;
    border: 1px solid #248742;
    border-radius: 8px;
    background-color: #F8F8F9;
    z-index: 2;
    position: relative;
    padding-left: 34px;
    padding-top: 14px;
    margin-top: 0;
    font-size: 13px;
    line-height: 1.8;
    letter-spacing: 0.25px;
    width: 100%;
    margin-bottom: -1px;
    padding-right: 28px;
    box-sizing: border-box;
    color: #464553;
    transition-duration: 0.25s;
}

/* Trip Column Two */
.trip__column--two {
    flex-wrap: wrap;
    border-top: 1px solid #F3F4F6;
    display: flex;
    height: 42px;
    
}





/* Trip Column Right */
.trip__column--right {
    height: 40px;
    overflow: visible;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    flex-grow: 1;
    
}

/* Trip Right Container */
.trip__right--container {
    display: block;
    
}

/* Trip Container Category */
 #trip-category-selector {
    width: 300px;
    border: 1px solid transparent;
    font-size: 12px;
    font-family: Axiforma, Helvetica, Arial, sans-serif;
    padding: 0;
    border-radius: 0;
    margin: 0;
    text-align: left;
    padding-bottom: 0px;
    background: white;
    line-height: 2;   
}

#trip-category-selector li {
    color: #687E98;
    border-radius: 8px;
    padding: 8px;
    background-color: transparent;
    transition: background-color 0.2s ease-in-out;
    cursor: pointer; 
    margin: 0 8px;
}

/* Trip List Category */
#trip-category-selector li:first-child {
    padding-right: 30px;
    padding-left: 18px;
    border-radius: 0;
    background-image: url(img/frontpage/icon/caratdown.png);
    background-size: 8px 5px;
    background-position: right 18px center;
    background-repeat: no-repeat;
    font-weight: 600;
    color: #687E98;
    border-radius: 8px;
    padding: 8px;
    background-color: transparent;
    transition: background-color 0.2s ease-in-out;
    cursor: pointer;
    margin: 0;
       
}

#trip-category-selector svg {
    width: 18px;
    height: 18px;
    display: inline-block;
    vertical-align: middle;
}

#trip-category-selector li svg:last-child {
    margin-right: 10px;
}



/* Trip Active */
#trip-category-selector li#trip__header-item.not-displayed {
    display: none;
    border-radius: 8px 8px 0px 0px;
    background-color: #EBF9F9;
    background-image: url(img/frontpage/icon/check.svg);
    background-repeat: no-repeat;
    background-position: right 20px center;
    color: #4C5661;
    margin-top: 8px;
    border-bottom: 1px solid #DDE1E6;
    font-weight: 600;
    margin-bottom: 10px;
    margin: 0 8px;
    padding: 8px;
    transition: background-color 0.2s ease-in-out;
    cursor: pointer;

}

#trip-category-selector li:not(:first-child) {
    margin: 0 8px;
    display: none;
    color: #687E98;
    border-radius: 8px;
    padding: 8px;
    cursor: pointer;
}


/* Trip Arrow */
.trip__arrow {
    display: inline-block;
    vertical-align: middle;
    margin-left: 5px;
    margin-right: 10px;
    margin-bottom: 2px;
    transition: all 0.2s ease-in-out;
}

/* Trip CTA */
.trip__cta {
    text-align: center;
    padding-bottom: 20px;
    padding: 20px 0;
    display: flex;
    flex-wrap: wrap;
    display: none;

    @include mobile {
        display: none;
    }
}

/* Trip CTA Text */
.trip__cta--text {
    position: relative;
    margin-left: 0.75%;
    margin-right: 0.75%;
    width: 98.5%;
    display: block;
}

.trip__link {
    transition: all 0.3s ease-in;
    color: #248742;
    text-decoration: none;
    cursor: pointer;
}

/* Trip Map Image */
.trip__map--image {
    position: relative;
    margin-left: 0.75%;
    margin-right: 0.75%;
    width: 98.5%;
    display: block;
}

/* Trip Map */
.trip__map {
    max-width: 750px;
    margin: 0 auto;
    display: block;
}

/* Trip Map Paragraph */
.trip__map--paragraph {
    display: block;
}

/* Trip Mobile Text */
.trip__map--paragraph {
   display: block;
}

.trip__mobile--text {
    text-align: center;
    font-size: calc(16px + (20 - 16) * ((100vw - 500px) / (850 - 500)));
    line-height: 1.55;
    display: block;

   @include mobile {
    
   }
}

